import { CONFIG } from "./config"
import { Hex } from "./hex"
import { loadChunk, unloadChunk } from "./mapProvider"
import { Point } from "./point"
import { range, unique } from "./utils"
import { applyOriginChangeCapability, applyPanCapability, applyRotateCapability, applyZoomCapability, preventContextMenuCapability } from "./viewCapabilities"

const map = new Map<string, Hex>()
const view = document.querySelector('#view')!

preventContextMenuCapability(view)
applyPanCapability(view)
applyZoomCapability(view, 0.3, 2.0, 0.1)
applyRotateCapability(view)
// applyOriginChangeCapability(view, async newOrigin => {
//     console.log('origin', newOrigin.valueOf())
//     await loadIntoMap(newOrigin)
//     await unloadFromMap(newOrigin)
// })

loadIntoMap(CONFIG.ORIGIN_POINT)


function loadIntoMap(origin: Point, distance: number = CONFIG.RENDER_DISTANCE) {
    range(origin.x - distance, origin.x + distance + 1)
    .flatMap(x => range(origin.y - distance, origin.y + distance + 1).map(y => new Point(x, y, origin.z)))
    .filter(chunk => chunk.distance3D(origin) <= distance)
    .forEach(async chunkPosition => {
        const result = await loadChunk(chunkPosition)
        if (result.length !== 0) {
            result.forEach(h => map.set(h.position.valueOf(), h))
            
            const chunk = document.createElementNS("http://www.w3.org/2000/svg", 'g')
            chunk.id = `chunk ${chunkPosition.valueOf()}`
            chunk.append(...result.map(h => h.draw()))
            
            const chunkDocument = document.createDocumentFragment()
            chunkDocument.append(chunk)
            view.append(chunkDocument)
        }
    })
}

